*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  text-align: center;
  padding: 40px;
  font-size: 22px;
}

.search-input {
  margin-right: 15px;
  margin-bottom: 15px;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 2px;
  padding-bottom: 6px;
  height: 40px;
  border-color: #36454f;

  border-width: 3px;
  border-radius: 5px;
  font-family: inherit;
  font-size: inherit;
  text-indent: 5px;

  outline: none;
}

.search-input::placeholder {
  opacity: 1;
  transition: opacity 0.5s ease;
}

.search-input:focus::placeholder {
  opacity: 0;
}

.search-form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.btn {
  height: 40px;
  min-width: 100px;
  margin-bottom: 15px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 6px;
  border-radius: 5px;

  background-color: #000000;
  color: #ffffff;
  font-family: inherit;
  font-size: inherit;
}

.btn:hover {
  background-color: #36454f;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  padding: 15px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.photo-card {
  width: 300px;
  margin: 10px;
  padding: 10px;
  border: 4px double black;
  overflow: hidden;
}

.stats {
  display: flex;
  justify-content: space-evenly;
}

.stats-item {
  display: flex;
  flex-direction: column;
  font-size: 18px;
}

.stats-item i {
  margin-bottom: 5px;
  color: #36454f;
}

.is-hidden {
  display: none;
}
